import { Component } from '@angular/core';
import { ApiService } from '../app/services/api.service';
import { NgForm } from '@angular/forms';

import { SimpleCompositeResponse } from './models/luisResponseModel';
import { Content } from './models/contentModel';
import { LuisRequestType } from './models/enums';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'AngularMVC';
  private luisResult: Array<SimpleCompositeResponse>;
  private changedLuisResults: Array<Content>;
  private content: Array<Content>;
  private luisLinesToCheck: string[] = [];
  private luisUrl: any;
  private labelForInput: string = "Enter update instruction:";

  ngOnInit() {
    this.content = [
      new Content(1, "GoF Definition"),
      new Content(2, "This chapter covers the Singleton pattern."),
      new Content(3, "GoF Definition"),
      new Content(4, "Ensure a class has only one instance, and provide a global point of access to it."),
      new Content(5, "Concept"),
      new Content(6, "A particular class should have only one instance. You can use this instance whenever "),
      new Content(7, "you need it and therefore avoid creating unnecessary objects."),
    ];
  }

  private AddToLuisCheck(newLineToCheck: NgForm) {
    this.luisLinesToCheck.push(newLineToCheck.value["luis-query"]);
    newLineToCheck.reset();
  }

  private GetLuisResult() {
    if (this.luisLinesToCheck.length > 0) {
      this.apiService.postDataAndReturnCompositeEntitie('/contentupdate/v1/check-composite', this.luisLinesToCheck, LuisRequestType.CompositeEntity)
        .subscribe(
        data => {
          this.luisResult = data;
          this.ProcessLuisResponseAndChanges(this.luisResult)
          //this.luisResult = data,
          //  console.log("data: ", data, "luisResult: ", this.luisResult)
        }
      );
//      this.ProcessLuisResponseAndChanges(this.content, this.luisResult);
    }
    console.log("Result: ", this.luisResult);
  }

  private RemoveItemFromArray(indexer: number) {
    this.luisLinesToCheck.splice(indexer, 1);
  }

  private ProcessLuisResponseAndChanges(responseData: Array<SimpleCompositeResponse>) {


    responseData.forEach((value: SimpleCompositeResponse) =>
    {
      let indexOfRecord = this.content.findIndex(x => x.lineNumber.toString() == value.LineToUpdate);
      let currentContent = this.content[indexOfRecord].content;
      this.content[indexOfRecord].changeToContent = currentContent.replace(value.UpdateFrom, "<del>" + value.UpdateFrom +  "</del><ins>" + value.UpdateTo + "</ins>");;
    })

    this.changedLuisResults = this.content.filter(v => v.changeToContent != "");

    //for (let change in responseData) {
    //  let number = change.LineToUpdate;
      
    //}
    //console.log(currentData);

    //let filteredList = luisResult.Children;
    //console.log(filteredList);
    //for (let luis of luisResult.Children) {
    //  if (existingContent.find(c => c.lineNumber == luis.))

    //}

  }

  constructor(private apiService: ApiService) {
  }


}
